@import 'assets/styles/variables';

.wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: $blue;
    color: $white;
    height: 100vh;
}

.title {
    font-size: 40px;
}

.text {
    margin: 20px 0;
}

.button {
    color: $blue;
    background: $white;
    height: 40px;
    border-radius: 5px;
    border: none;
    cursor: pointer;
    font-size: 14px;

    // &:hover{ }
}